<section class="">
1、本活动为汽车之家用户专享。 
2、在超品特价车活动车源下，用户支付定金，获得该车源所展示权益，用户可以在“汽车之家App-我的-我的订单”中找到订单。
3、权益一：购车返现1500元
3.1、成功支付定单后，会有商家联系您进行线下看车。完成车辆购买后，用户需在活动期间（11.9-12.31）内在“汽车之家App-我的-我的订单”相应车源定单中上传留资车源的购车凭证。
3.2、购车凭证（购车发票、购车合同）须与下定单用户身份一致。
3.3、汽车之家将会以购买人上传购车凭证时间顺序进行审核，以前250名用户审核通过时间为准进行返现。购车补贴奖励将会在审核通过核销完成后15个工作日内发放至下定单用户的二手车之家账户，用户登录“二手车之家APP-我的-钱包”即可提现。
3.4、本次活动返现名额有限，先到先得。
4、权益二：免费维保查询 
4.1、成功支付定单后，之家将发送一张价值16元的维保查询券，您可以在“二手车之家APP-我的-优惠券“查看；
4.2、维保查询券可应用于所有维保可查车源；
4.3、维保查询券使用后，9.9元定金将不可退。

